header {
    display: flex;
    align-items: center;
    min-height: 200px;
    background-clip:content-box;
    background-repeat: no-repeat;
    width: 100%;
    background-size: contain;
    background-color: var(--menu-contrast);
    overflow: hidden;
    /* background-attachment: fixed; */
    background-position:left top;
  }
  
  header h1 {
    font-size: 40px;

    /* color: var(--menu-color) */
  }

  header div {
    padding: 2em;
    padding-left: 10em;
    color: var(--text-accent);
  }
  
  header div *{
    margin: 10px
  }